#click-here{color:#495925}.garden-items{width:100%;display:flex;margin:10px;justify-content:space-between;align-items:flex-end;position:absolute;bottom:0}.garden-items img{-o-object-fit:contain;object-fit:contain}.play-controls{display:flex;flex-direction:column;justify-content:center;height:70px;width:200px;align-self:center;background-color:#93A64E;box-shadow:5px 5px rgba(38,19,8,0.5);font-size:18px;border:1px solid #261308;border-radius:6px;padding:15px;margin-right:50px;box-sizing:content-box}.play-controls p{margin-bottom:5px}.play-controls i{margin-right:7px;transition:color 0.2s}.play-controls i:hover{color:#A6483F;text-shadow:3px 3px rgba(38,19,8,0.2);transition:color 0.2s;cursor:pointer}
